import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      settings: {
        config: []
      },
      loading: false,
      saving: false
    };
  },
  computed: {
    ...mapGetters(['credentials'])
  },
  methods: {
    async fetchWithAuth(url, options = {}) {
      if (this.credentials) {
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    async saveSettings() {
      this.saving = true;
      try {
        await this.fetchWithAuth('/api/manage/sysConfig/page', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.settings)
        });
        this.$message({
          message: '设置已成功保存',
          type: 'success',
          icon: '<i class="fas fa-check-circle"></i>'
        });
      } catch (error) {
        this.$message.error('保存设置时出错，请重试');
        console.error('保存设置错误:', error);
      } finally {
        this.saving = false;
      }
    }
  },
  async mounted() {
    this.loading = true;
    try {
      const response = await this.fetchWithAuth('/api/manage/sysConfig/page');
      const data = await response.json();
      this.settings = data;
    } catch (error) {
      this.$message.error('加载设置失败');
      console.error('加载设置错误:', error);
    } finally {
      this.loading = false;
    }
  }
};