export default {
  name: 'SysConfigTabs',
  props: {
    activeIndex: {
      type: String,
      default: 'upload'
    },
    isCollapse: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isCollapsed: this.isCollapse
    };
  },
  watch: {
    // 监听 prop 变化
    isCollapse(newVal) {
      this.isCollapsed = newVal;
    }
  },
  methods: {
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed;
      this.$emit('update:isCollapse', this.isCollapsed);
    },
    checkMobile() {
      const isMobile = window.innerWidth <= 768;
      this.isCollapsed = isMobile;
      this.$emit('update:isCollapse', this.isCollapsed);
    },
    handleSelect(index) {
      this.$emit('update:activeIndex', index);

      // 在移动设备上，选择菜单项后自动折叠
      if (window.innerWidth <= 768) {
        this.isCollapsed = true;
        this.$emit('update:isCollapse', true);
      }
    }
  },
  mounted() {
    this.checkMobile();
    window.addEventListener('resize', this.checkMobile);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile);
  }
};