import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      // 一级设置：上传渠道
      channels: [{
        value: 'telegram',
        label: 'Telegram'
      }, {
        value: 'cfr2',
        label: 'CloudFlare R2'
      }, {
        value: 's3',
        label: 'S3'
      }],
      activeChannel: 'telegram',
      // 当前选中的上传渠道

      // 二级设置：Telegram 配置
      telegramSettings: {
        loadBalance: {},
        channels: []
      },
      tgRules: {
        name: [{
          required: true,
          message: '请输入渠道名',
          trigger: 'blur'
        }, {
          validator: (rule, value, callback) => {
            const names = this.telegramSettings.channels.map(item => item.name);
            if (names.filter(name => name === value).length > 1) {
              callback(new Error('渠道名不能重复'));
            } else if (value === 'Telegram_env') {
              // 判断该渠道保存位置是否为环境变量
              const savePath = this.telegramSettings.channels.find(item => item.name === value).savePath;
              if (savePath !== 'environment variable') {
                callback(new Error('渠道名不能为保留值'));
              } else {
                callback();
              }
            } else {
              callback();
            }
          },
          trigger: 'blur'
        }],
        botToken: [{
          required: true,
          message: '请输入 Bot Token',
          trigger: 'blur'
        }],
        chatId: [{
          required: true,
          message: '请输入 Chat ID',
          trigger: 'blur'
        }]
      },
      // 二级设置：CFR2 配置
      cfr2Settings: {
        channels: []
      },
      // 二级设置：S3 配置
      s3Settings: {
        loadBalance: {},
        channels: []
      },
      s3Rules: {
        name: [{
          required: true,
          message: '请输入渠道名',
          trigger: 'blur'
        }, {
          validator: (rule, value, callback) => {
            const names = this.s3Settings.channels.map(item => item.name);
            if (names.filter(name => name === value).length > 1) {
              callback(new Error('渠道名不能重复'));
            } else if (value === 'S3_env') {
              // 判断该渠道保存位置是否为环境变量
              const savePath = this.s3Settings.channels.find(item => item.name === value).savePath;
              if (savePath !== 'environment variable') {
                callback(new Error('渠道名不能为保留值'));
              } else {
                callback();
              }
            } else {
              callback();
            }
          },
          trigger: 'blur'
        }],
        endpoint: [{
          required: true,
          message: '请输入 Endpoint',
          trigger: 'blur'
        }],
        bucketName: [{
          required: true,
          message: '请输入存储桶名称',
          trigger: 'blur'
        }],
        region: [{
          required: true,
          message: '请输入存储桶区域',
          trigger: 'blur'
        }],
        accessKeyId: [{
          required: true,
          message: '请输入访问密钥 ID',
          trigger: 'blur'
        }],
        secretAccessKey: [{
          required: true,
          message: '请输入机密访问密钥',
          trigger: 'blur'
        }]
      },
      // 加载状态
      loading: false,
      saving: false
    };
  },
  computed: {
    ...mapGetters(['credentials']),
    // 当前选中渠道的标签
    activeChannelLabel() {
      const channel = this.channels.find(item => item.value === this.activeChannel);
      return channel ? channel.label : '';
    }
  },
  methods: {
    getChannelIcon(channelType) {
      const icons = {
        telegram: 'paper-plane',
        cfr2: 'cloud',
        s3: 'database'
      };
      return icons[channelType] || 'cloud-upload-alt';
    },
    async fetchWithAuth(url, options = {}) {
      if (this.credentials) {
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    addChannel() {
      switch (this.activeChannel) {
        case 'telegram':
          this.telegramSettings.channels.push({
            id: this.telegramSettings.channels.length + 1,
            name: '',
            type: 'telegram',
            savePath: 'database',
            botToken: '',
            chatId: '',
            enabled: true,
            fixed: false
          });
          break;
        case 'cfr2':
          this.$message({
            message: '暂不支持增加 R2渠道 配置',
            type: 'warning',
            icon: '<i class="fas fa-exclamation-circle"></i>'
          });
          break;
        case 's3':
          this.s3Settings.channels.push({
            id: this.s3Settings.channels.length + 1,
            name: '',
            type: 's3',
            savePath: 'database',
            accessKeyId: '',
            secretAccessKey: '',
            region: '',
            bucketName: '',
            endpoint: '',
            enabled: true,
            fixed: false
          });
          break;
      }
    },
    deleteChannel(index) {
      switch (this.activeChannel) {
        case 'telegram':
          // 调整 id
          this.telegramSettings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.telegramSettings.channels.splice(index, 1);
          break;
        case 'cfr2':
          // 调整 id
          this.cfr2Settings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.cfr2Settings.channels.splice(index, 1);
          break;
        case 's3':
          // 调整 id
          this.s3Settings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.s3Settings.channels.splice(index, 1);
          break;
      }
    },
    async saveSettings() {
      // 所有表单的 Promise 数组
      let validationPromises = [];

      // Telegram
      if (this.$refs.tgChannelForm) {
        this.$refs.tgChannelForm.forEach(form => {
          validationPromises.push(new Promise(resolve => {
            form.validate(valid => resolve(valid));
          }));
        });
      }

      // S3
      if (this.$refs.s3ChannelForm) {
        this.$refs.s3ChannelForm.forEach(form => {
          validationPromises.push(new Promise(resolve => {
            form.validate(valid => resolve(valid));
          }));
        });
      }

      // 等待所有验证完成
      const results = await Promise.all(validationPromises);
      const isValid = results.every(valid => valid);
      if (!isValid) {
        this.$message.warning('请检查表单填写是否正确');
        return;
      }
      this.saving = true;
      try {
        // 保存设置
        const settings = {
          telegram: this.telegramSettings,
          cfr2: this.cfr2Settings,
          s3: this.s3Settings
        };
        await this.fetchWithAuth('/api/manage/sysConfig/upload', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(settings)
        });
        this.$message({
          message: '上传设置已成功保存',
          type: 'success',
          icon: '<i class="fas fa-check-circle"></i>'
        });
      } catch (error) {
        this.$message.error('保存设置时出错，请重试');
        console.error('保存设置错误:', error);
      } finally {
        this.saving = false;
      }
    }
  },
  async mounted() {
    this.loading = true;
    try {
      // 获取上传设置
      const response = await this.fetchWithAuth('/api/manage/sysConfig/upload');
      const data = await response.json();
      this.telegramSettings = data.telegram;
      this.cfr2Settings = data.cfr2;
      this.s3Settings = data.s3;
    } catch (error) {
      this.$message.error('加载上传设置失败');
      console.error('加载设置错误:', error);
    } finally {
      this.loading = false;
    }
  }
};